.empty {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #707070;
  text-shadow: 2px 2px 0 #eee, 3px 3px 0 #707070;
}

.empty h1 {
  padding: 1.2em;
  text-align: center;
}
.empty .scene {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 300px;
  height: 200px;
}
.empty .plane,
.empty .cloud {
  position: absolute;
}
/*plane animation*/
.empty .plane {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: anim-plane;
          animation-name: anim-plane;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;

  -webkit-animation-fill-mode: forwards;

          animation-fill-mode: forwards;
  display: block;
  margin: 0 auto;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  left: 30%;
}

@-webkit-keyframes anim-plane {
  to {
    -webkit-transform: translateY(95px);
            transform: translateY(95px);
  }
}

@keyframes anim-plane {
  to {
    -webkit-transform: translateY(95px);
            transform: translateY(95px);
  }
}

/* Cloud Animation */

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes move {
  from {
    left: 200px;
  }
  to {
    left: 0px;
  }
}

@keyframes move {
  from {
    left: 200px;
  }
  to {
    left: 0px;
  }
}

.cloud {
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-name: move, fade;
          animation-name: move, fade;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;

  display: block;
  background: url(data:image/svg+xml;base64,);
  height: 40px;
  width: 53px;
  margin: 0 auto;
}
.cloud--small {
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  top: 65px;
  -webkit-transform: scaleX(0.5) scaleY(0.5);
          transform: scaleX(0.5) scaleY(0.5);
}
.cloud--medium {
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  top: 95px;
  -webkit-transform: scaleX(0.7) scaleY(0.7);
          transform: scaleX(0.7) scaleY(0.7);
}
.cloud--large {
  -webkit-animation-duration: 4.5s;
          animation-duration: 4.5s;
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
  top: 95px;
  -webkit-transform: scaleX(0.8) scaleY(0.8);
          transform: scaleX(0.8) scaleY(0.8);
}

.whoami {
  padding-top: 3em;
  text-align: center;
}
